import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';
import Minipedro2 from './img/MINIDomPedroII.png'
import MINIdompedro11estatua from './img/MINIdompedro11estatua.png'
import MINIluizgamaestampa from './img/MINIluizgamaestampa.png'
import MINIMARIOCHET from './img/MINIMARIOCHET.png'
import MINIprincesaisabel from './img/MINIprincesaisabel.png'
import MINIprincipesamurai from './img/MINIprincipesamurai.png'

class Galeria extends Component {
    render(){
        return(
            <div>
                <h2 style={{textAlign: 'center'}}>Galeria de Artes</h2>
            <Grid className="grid-galeria">
                <Cell col={4} >
                    <img src={Minipedro2} style={{width: '90%', height: '90%', paddingLeft: '25px'}} />
                </Cell>
                <Cell col={4} >
                    <img src={MINIdompedro11estatua} style={{width: '90%', height: '90%', paddingLeft: '25px'}}/>
                </Cell>
                <Cell col={4} >
                    <img src={MINIluizgamaestampa} style={{width: '90%', height: '90%', paddingLeft: '25px'}}/>
                </Cell>

                <Cell col={4} >
                    <img src={MINIMARIOCHET} style={{width: '90%', height: '90%', paddingLeft: '25px'}} />
                </Cell>
                <Cell col={4} >
                    <img src={MINIprincesaisabel} style={{width: '90%', height: '90%', paddingLeft: '25px'}} />
                </Cell>
                <Cell col={4} >
                    <img src={MINIprincipesamurai} style={{width: '90%', height: '90%', paddingLeft: '25px'}} />
                </Cell>
            </Grid>
            </div>
            
       
        )
    }
}
    

export default Galeria;