import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Formacao from './formacao';
import Experiencia from './experiencia';
import Conhecimentos from './conhecimentos'
import Informacoes from './informacoes';
import Jorgetrabalhador from './euTrabalhador.png'

class Resume extends Component {
    render () {
        return(
            <div>
                <Grid className="resume-grid">
                    <Cell className="resume-coluna-esquerda" col={4}>
                        <div style={{textAlign: 'center'}}>
                            <img src={Jorgetrabalhador}
                            alt="avatar"
                            style={{height: '200px'}}/>
                            <h2 style={{paddingTop: '1em'}}>Jorge Rinaldi</h2>
                            <h4 style={{color: 'white'}}>Desenvolvedor Front End</h4>
                            <h4 style={{color: 'white'}}>Web Designer</h4>
                            <hr style={{borderTop: '3px solid #342323', width: '100%'}}/></div>
                            <p style={{fontSize: '15px'}}> Olá, eu me chamo Jorge Rinaldi, sou criativo, proativo, sei trabalhar em equipe e possuo experiência profissional
                        como Web Designer e Desenvolvedor Front-End (apesar do pouco tempo exercendo esta função, eu pude adquiri bastante aprendizado).
                        Estou cursando o ultimo ano de Analise e Desenvolvimento de Sistemas, e me coloco a disposição de futuras entrevistas e demais procedimentos que se fizerem necessarios. </p>
                            <hr style={{borderTop: '3px solid #342323', width: '100%'}}/>
                            
                            <Informacoes 
                                Endereco={'Endereço'}
                                rua={'Rua Pera do Norte, N° 321,'}
                                bairro={'Jd Santa Lucreia - Jaraguá'}
                                Cep={'CEP'}
                                cep={'05185-390'}
                                Estadocivil={'Estado Civil'}
                                solteiro={'Solteiro'}
                                Idade={'Idade'}
                                anos={'25'}
                            />

                            </Cell>
                    <Cell className="resume-coluna-direita" style={{paddingTop: '0px'}} col={8}>
                        <h2>Formação</h2>
                        <Formacao 
                            Duracao={'Duração'}
                            startYear={2016}
                            endYear={2020}
                            Fatec={'Fatec Taquaritinga'}
                            curso={'Técnologo em Analise e Desenvolvimento de Sistemas'}
                        />

                        <hr style={{borderTop: '3px solid #342323', width: '100%'}}/>

                        <h2>Experiência</h2>
                        <Experiencia
                        Empresa={'Farmacia Santa Tereza'}
                        duracao={'Período'}
                        startYear={'22/06/2018'}
                        endYear={'19/10/2018'}
                        funcao={'Web Designer'}
                        atividades={'Web Design Responsivo, Criação de Banners, Divulgação dos Produtos da Loja, SEO, e Atendimento ao Cliente através de mídias sociais'}/>

                        <Experiencia
                        Empresa={'Hydra Consultoria'}
                        duracao={'Período'}
                        startYear={'11/03/2019'}
                        endYear={'05/04/2019'}
                        funcao={'Desenvolvedor Front-End / Suporte Técnico'}
                        atividades={'Desenvolvimento de Front End usando React, UI/UX e Suporte Técnico realizando Procedures SQL'}/>

                        <hr style={{borderTop: '3px solid #342323', width: '100%'}}/>
                        <h2>Conhecimentos</h2>

                        <Conhecimentos 
                        skills="HTML"
                        progress={100}/>
                        <Conhecimentos 
                        skills="CSS"
                        progress={75}/>
                        <Conhecimentos 
                        skills="Boostrap"
                        progress={90}/>
                        <Conhecimentos 
                        skills="React"
                        progress={67}/>
                        <Conhecimentos 
                        skills="Javascript"
                        progress={55}/>
                        <Conhecimentos 
                        skills="SQL"
                        progress={78}/>
                        <Conhecimentos 
                        skills="Pacote Adobe"
                        progress={70}/>
                        
                        


                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Resume;