import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class Informacoes extends Component {
    render(){
        return(
            <Grid>
                
                <Cell col={8}>
                    <h4 style={{marginTop:'0px'}}>{this.props.Endereco}</h4>
                    <p>{this.props.rua}</p>
                    <p>{this.props.bairro}</p>
                </Cell>
                <Cell col={4}>
                    <h4 style={{marginTop:'0px'}}>{this.props.Cep}</h4>
                    <p>{this.props.cep}</p>
                </Cell>
                <Cell col={8}>
                    <h4 style={{marginTop:'0px'}}>{this.props.Estadocivil}</h4>
                    <p>{this.props.solteiro}</p>
                </Cell>
                <Cell col={4}>
                    <h4 style={{marginTop:'0px'}}>{this.props.Idade}</h4>
                    <p>{this.props.anos}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Informacoes;