import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class Experiencia extends Component {
    render() {
        return(
            <Grid>
                <Cell col={12}>
                   <h3 style={{marginTop:'0px'}}>{this.props.Empresa}</h3>
                </Cell>
                <Cell col={6}>
                    <h4 style={{marginTop:'0px'}}>{this.props.duracao}</h4>
                    <p>{this.props.startYear} - {this.props.endYear}</p>
                </Cell>
                <Cell col={6}>
                    <h4 style={{marginTop:'0px'}}>{this.props.funcao}</h4>
                    <p>{this.props.atividades}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Experiencia;