import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl'
import Escritorio from './escritorio.png'


class Contato extends Component {
    render () {
        return(
            <div className="contato-body">
                <Grid className="contato-grid">
                    <Cell col={6}>
                        <h2>Se interessou em meus serviços?</h2>
                        <img src={Escritorio}
                        alt="avatar" style={{height: '240px'}}
                        />
                        <h2>Sinta-se a vontade para entrar em contato</h2>
                    </Cell>
                    <Cell col={6}>
                        <h2>Contato</h2>
                    <div className="contato-list">
                        <List>
                            <ListItem>
                                <ListItemContent style={{fontSize: '25px', fontFamily: 'Alata'}}><i className="fa fa-whatsapp" aria-hidden="true"/>(16) 99606-6839</ListItemContent>
                            </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '25px', fontFamily: 'Alata'}}><i className="fa fa-envelope" aria-hidden="true"/>jorge.rinaldi.1995@gmail.com</ListItemContent>
                            </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '25px', fontFamily: 'Alata'}}><i className="fa fa-linkedin-square" aria-hidden="true"/><a href="https://www.linkedin.com/in/jorge-adriano-rinaldi-costa-62850a17b/" rel="nooponer noreferrer" target="_blank">Linkedin</a></ListItemContent>
                            </ListItem>
                                                        
                        </List>
                    </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contato;