import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Eu from './eu.png'


class Landingpage extends Component {
    render () {
        return(
            <div style={{width: '100%', margin: 'auto', paddingTop: '30px'}}>
                <Grid className="landing-grid">
                    <Cell col={4} className="esquerda">
                    <img 
                     src={Eu}
                     alt="avatar"
                     className="avatar-img"
                    />
                    </Cell>
                    <Cell col={8} className="direita">

                    <div className="banner-text">
                        <h1>Jorge Rinaldi</h1>
                        <h2>Web Designer</h2>

                    

                        <div className="redes-sociais">
                            <a href="https://github.com/JorgeRinaldi1995" rel="nooponer noreferrer" target="_blank">
                                <i className="fa fa-github-square" aria-hidden="true" />
                            </a>

                            <a href="https://www.facebook.com/jorge.rinaldi.399" rel="nooponer noreferrer" target="_blank">
                                <i className="fa fa-facebook-square" aria-hidden="true" />
                            </a>

                            <a href="https://www.linkedin.com/in/jorge-adriano-rinaldi-costa-62850a17b/" rel="nooponer noreferrer" target="_blank">
                                <i className="fa fa-linkedin-square" aria-hidden="true" />
                            </a>

                            <a href="https://www.instagram.com/rinaldijorge/?hl=pt-br" rel="nooponer noreferrer" target="_blank">
                                <i className="fa fa-instagram" aria-hidden="true" />
                            </a>
                        </div>
                    </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}


export default Landingpage;